/// <reference types="googlemaps" />
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Subscription, empty } from 'rxjs';
import { ContactService } from '../contact.service';

@Component({
  selector: 'app-contactme',
  templateUrl: './contactme.component.html',
  styleUrls: ['./contactme.component.css'],
})
export class ContactmeComponent implements OnInit, OnDestroy {
  constructor(private contact: ContactService) {}
  public subscription: Subscription;

  contactMe = { name: '', subject: '', email: '', comment: '' };
  formTitle = {
    name: 'Required',
    email: 'Enter valid Email Address',
    subject: 'Required',
    comment: 'Min 3 characters',
  };
  contactForm: FormGroup;

  ngOnInit(): void {
    let body = document.querySelector('body');
    body.classList.remove('container');

    let title = <HTMLElement[]>(<any>document.querySelectorAll('.title span'));

    let counter = 0,
      widthLoading = 0;
    let loaderWrapper = document.querySelector('.loader-wrapper');
    let loadingComplete = <HTMLElement>(
      document.querySelector('.loading-complete')
    );

    const animationArray = ['hideUp', 'hideDown', 'hideLeft', 'hideRight'];

    let percentage = document.createElement('div');
    percentage.classList.add('loading-percentage');
    loaderWrapper.appendChild(percentage);

    var interval = setInterval(() => {
      loadingComplete.style.width = widthLoading + '%';
      counter++;
      widthLoading++;
      percentage.innerHTML = counter - 1 + '%';
      if (counter == 101) {
        loaderWrapper.classList.add(
          animationArray[Math.floor(Math.random() * 4)]
        );
        clearInterval(interval);
      }
    }, 10);

    setTimeout(() => {
      let delay = 0;
      title.forEach((spanEle) => {
        spanEle.style.animation = `comeout 1s ease ${delay}s forwards`;
        delay += 0.05;
      });
    }, 2800);

    var markerPos = { lat: 12.9698, lng: 77.75 };
    12.9141;
    var uluru = { lat: 12.918, lng: 75.94 };
    var map = new google.maps.Map(
      document.getElementById('map') as HTMLElement,
      {
        zoom: 7,
        center: uluru,
        mapTypeControl: false,
        // disableDefaultUI: true,
        styles: [
          { elementType: 'geometry', stylers: [{ color: '#a34d45' }] },
          {
            elementType: 'labels.text.stroke',
            stylers: [{ color: '#000000' }],
          },
          {
            elementType: 'labels.text.fill',
            stylers: [{ color: '#ffffff' }],
          },
          {
            featureType: 'water',
            elementType: 'geometry',
            stylers: [{ color: '#282626' }],
          },
          {
            featureType: 'road',
            elementType: 'geometry',
            stylers: [{ color: '#000000' }],
          },
        ],
      }
    );
    const icon = './assets/images/map_marker.png';
    var marker = new google.maps.Marker({
      position: markerPos,
      map: map,
      title: 'My Residence!',
      icon: icon,
    });

    title.forEach((spanTag) => {
      spanTag.addEventListener('mouseover', () => {
        spanTag.style.opacity = '1';
        spanTag.style.animation = '';
        spanTag.classList.toggle('bounce');
      });
    });

    this.contactForm = new FormGroup({
      name: new FormControl(this.contactMe.name, [Validators.required]),
      email: new FormControl(this.contactMe.email),
      subject: new FormControl(this.contactMe.subject),
      comments: new FormControl(this.contactMe.comment, [
        Validators.minLength(3),
      ]),
    });
  }

  get name() {
    return this.contactForm.get('name');
  }
  get email() {
    return this.contactForm.get('email');
  }
  get subject() {
    return this.contactForm.get('subject');
  }
  get comment() {
    return this.contactForm.get('comments');
  }
  onSubmit() {
    this.contactMe.name = this.contactForm.value.name;
    this.contactMe.subject = this.contactForm.value.subject;
    this.contactMe.email = this.contactForm.value.email;
    this.contactMe.comment = this.contactForm.value.comments;
    // console.log(this.contactMe.email + ' Hello');

    this.subscription = this.contact.sendEmail(this.contactMe).subscribe(
      (data) => {
        let msg = data['message'];
        // alert(msg);
        console.log('success');
        info.classList.remove('wait');
        info.classList.add('success');
        setTimeout(function () {
          info.classList.remove('success');
        }, 3000);
      },
      (error) => {
        console.error(error, 'error');
        info.classList.add('error');
        setTimeout(function () {
          info.classList.remove('error');
        }, 3000);
      }
    );
    this.contactForm.reset();

    let info = <HTMLElement>document.querySelector('.info');
    info.classList.add('wait');
  }

  ngOnDestroy() {
    let body = document.querySelector('body');
    body.classList.add('container');
  }
}
